<template>
  <el-row :gutter="20">
    <el-col :span="8" class="db-type">
      {{mapping.columnType}}:
    </el-col>
    <el-col :span="16">
      <el-select placeholder="请选择映射类型"  :name="mapping.columnType" v-model="mapping.fullName">
        <el-option v-for="(javaType,index) in javaTypes" :key="index" :label="javaType.fullName" :value="javaType.fullName"></el-option>
      </el-select>
    </el-col>
  </el-row>
</template>

<script>

  import {mapGetters} from "vuex";

  export default {
    props:["mapping"],
    name: "DbType2JavaType",
    computed:{
      ...mapGetters(["javaTypes"])
    }

  }


</script>

<style scoped>
  .db-type {
    padding: 10px;
    text-align: right;
  }
  .el-row{
    margin-bottom: 5px;
  }
</style>



